
.scene-enter{
  position:absolute;
  z-index:100;
  left:0;
  top:0;
  -webkit-transform:translateX(-100%);
          transform:translateX(-100%);
}
.scene-enter-active,.scene-leave-active{
  transition:all 0.2s;
}
.scene-leave-to{
  position:absolute;
  z-index:100;
  left:0;
  top:0;
  -webkit-transform:translateX(100%);
          transform:translateX(100%);
}




